import { ICartItem, IPizza } from '../@types/types';
import React, { useState } from 'react';

import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { addItem } from '../store/slices/cartSlice';
import { RootState } from 'store/store';

interface IPizzaBlockProps {
  pizza: IPizza;
}

export default function PizzaBlock({ pizza }: IPizzaBlockProps) {
  const dispatch = useDispatch();
  const cartItem = useSelector((state:RootState) =>
    state.cart.items.find((item) => item.id === pizza.id)
  );
  const addedCount =  cartItem ? cartItem.count : 0; 

  const [activeType, setActiveType] = useState(0);
  const [activeSize, setActiveSize] = useState(0);

  const typeNames = ['', ''];

  function onClickAdd() {
    const item: ICartItem = {
      id: pizza.id,
      name: pizza.name,
      price: pizza.price,
      imageUrl: pizza.imageUrl,
      type: typeNames[activeType],
      size: activeSize,
    };
    dispatch(addItem(item));
  }

  return (
    <div className='pizza-block'>
      <Link to={`/pizza/${pizza.id}`}>
        <img className='pizza-block__image' src={pizza.imageUrl} alt='Pizza' />
      </Link>
      <Link to={`/pizza/${pizza.id}`}>
      <h4 className='pizza-block__title'>{pizza.name}</h4>
      </Link>
      <div className='pizza-block__selector'>
        <ul>
          {pizza.types.map((type) => (
            <li
              key={type}
              className={activeType === type ? 'active' : ''}
              onClick={() => setActiveType(type)}
            >
              {typeNames[type]}
            </li>
          ))}
        </ul>
        <ul>
          {pizza.sizes.map((size) => (
            <li
              key={size}
              className={activeSize === size ? 'active' : ''}
              onClick={() => setActiveSize(size)}
            >
              {size}.
            </li>
          ))}
        </ul>
      </div>
      <div className='pizza-block__bottom'>
        <div className='pizza-block__price'> {pizza.price} </div>
        <button
          onClick={onClickAdd}
          className='button button--outline button--add'
        >
          <svg
            width='12'
            height='12'
            viewBox='0 0 12 12'
            fill='none'
            xmlns='http://www.w3.org/2000/svg'
          >
            <path
              d='M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z'
              fill='white'
            />
          </svg>
          <span></span>
          {addedCount > 0 ? <i>{addedCount}</i> : ''}
        </button>
      </div>
    </div>
  );
}
